import React from "react"
import { graphql, useStaticQuery } from 'gatsby'
import Layout from "../components/global/global_layout"
import ColumnOf2 from "../components/layout/column_of_2"
import KeyVisual from "../components/partial/key_visual"
import BlogList from "../components/partial/blog_list"
import PlofileCard from "../components/partial/profile_card"
import SideBar from "../components/partial/side_bar"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize } from '../util/style_property'
import SEO from "../components/seo"

const Wrapper = styled.div`
  width: 100%;
  margin: auto;
  background-color: ${color.background_dark_gray};
`


const IndexPage = () => {
  const data = useStaticQuery(
    graphql`
      query{
        allContentfulArticle(sort: {
          fields: updatedAt,
          order: DESC
        }){
          edges{
            node{
              title
              slug
              tags{
                name
                slug
              }
              updatedAt(formatString: "YYYY-MM-DD")
              thumbnail{
                fluid{
                  src
                }
              }
            }
          }
        }
      }
    `
  )
  return(<Layout>
    <SEO title="トップページ" />
    <KeyVisual isTop = {true}/>
    <Wrapper>
      <ColumnOf2
        main={<BlogList edges={data.allContentfulArticle.edges}/>}
        aside={<SideBar/>}
      />
    </Wrapper>
  </Layout>)
}



export default IndexPage
